// DOYOGA，做你的瑜珈
.hero {
    background: url("../../assets/images/photo-1612157777902-5382bc6e864b.jpeg"); /*  圖片水平垂直置中  */
    background-position: center bottom;
    background-size: cover; /*  背景自適應延伸屬性  */
    height: 560px;
    @include medium (){
        height: 582px;
    }    
}

// 頂尖師資、專業教學
.teacher-img-lg {
    width: 48px;
    @include medium (){
        width: 94px;
    }
}

.teacher-img-sm {
    width: 48px;
    @include medium (){
        width: 68px;
    }
}

// 舒適空間、靜心放鬆
.space-img-sm {
    
    height: 210px;
    @include medium (){
        width: 90px;
        height: 117px;
    }
    @include large (){
        width: 160px;
        height: 207px;
    }
}

.space-img-lg {
    height: 210px;
    @include medium (){        
        height: 266px;
    }
    @include large (){        
        height: 444px;
    }
}